@charset "UTF-8";
@import "../node_modules/normalize.css/normalize.css";
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/PTSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/PTSans-Bold.woff") format("woff"), url("../fonts/PTSans-Bold.woff2") format("woff2");
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  height: 100%;
  min-width: 320px;
  font-family: "PT Sans", Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #333333;
  text-transform: uppercase;
  line-height: 21px;
}

h1, h2, h3, h4 {
  color: #000000;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-height: 100%;
  padding-bottom: 258px;
}

@media (min-width: 768px) {
  .container {
    padding-bottom: 120px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .header {
    position: relative;
    max-width: 767px;
  }

  .header-navigation {
    min-height: 55px;
    background-color: #FFFFFF;
  }
}
.header-logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  z-index: 2;
}

.header-navigation__toggle {
  position: absolute;
  padding: 0;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  z-index: 2;
  background-color: #FFFFFF;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .header-navigation__toggle {
    display: none;
  }
}

.header-navigation--closed .header-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 8px 0 0 #81B3D2, 0 16px 0 0 #81B3D2;
}

.header-navigation--closed .header-navigation__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.7);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.7), 0 16px 0 0 rgba(129, 179, 210, 0.7);
}

.header-navigation--closed .header-navigation__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 16px 0 0 rgba(129, 179, 210, 0.3);
}

.header-navigation--opened .header-navigation__toggle {
  z-index: 11;
  background-color: #81B3D2;
}
.header-navigation--opened .header-navigation__toggle::before, .header-navigation--opened .header-navigation__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}
.header-navigation--opened .header-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header-navigation--opened .header-navigation__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .header-navigation--closed .header-navigation__item {
    display: none;
  }

  .header-navigation--opened .header-navigation__list {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: #81B3D2;
  }

  .header-navigation--nojs .header-navigation__toggle {
    display: none;
  }

  .header-navigation--nojs .header-navigation__list {
    position: static;
    min-height: 0;
  }

  .header-navigation--nojs .header-navigation__item {
    display: block;
  }

  .header-navigation--nojs {
    position: relative;
    left: 0;
    right: 0;
    z-index: 10;
    display: block;
    background-color: #FFFFFF;
  }
}
.header-navigation__list {
  font-weight: 700;
  color: #FFFFFF;
  background-color: #81B3D2;
}

.header-navigation__item {
  text-align: center;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  padding: 17px 0 12px;
}

@media (min-width: 768px) {
  .header-navigation__list {
    color: #000000;
    background-color: #FFFFFF;
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 1fr;
    padding: 18px 30px;
  }

  .header-navigation__item {
    box-shadow: none;
    padding: 0;
  }
  .header-navigation__item:hover {
    color: #81B3D2;
  }
  .header-navigation__item:active {
    color: #A9A9A9;
  }
  .header-navigation__item--current {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .header-navigation__list {
    padding: 18px 72px;
  }
}
