.button {
  cursor: pointer;
  border: none;
  font-weight: 700;
  line-height: 26px;
  color: #FFFFFF;
  background-color: #81B3D2;
  text-transform: uppercase;
}

.button--plus,
.button--minus {
  background-color: transparent;
}

.button--plus:hover path,
.button--minus:hover rect {
  fill: #000000;
}

.button--plus:active path,
.button--minus:active rect {
  fill: #81B3D2;
}

.button--minus svg {
  display: block;
}

.promo {
  text-align: center;
  margin: 32px 0;
  padding: 0 20px;
}

.promo p:first-child {
  margin-bottom: 35px;
  line-height: 26px;
  font-weight: 700;
  font-size: 18px;
  padding: 0 10px;
}

.promo br {
  display: none;
}

@media (min-width: 597px) {
  .promo br {
    display: block;
  }
}
@media (min-width: 768px) {
  .promo {
    margin: 47px 0 55px;
  }

  .promo p:first-child {
    font-size: 21px;
    padding: 0;
    margin-bottom: 30px;
  }
}
@media (min-width: 768px) {
  .promo {
    margin: 49px 0 44px;
  }
}
