.features__list {
  background-color: #EEEEEE;
}

.features__item h3 {
  font-size: 18px;
  line-height: 26px;
}

.features__text {
  text-align: center;
  padding: 0 20px;
}
.features__text h3 {
  padding-bottom: 11px;
}
.features__text br {
  display: none;
}
.features__text p {
  padding-top: 17px;
}

.features__item--picture {
  background-color: #81B3D2;
  color: #FFFFFF;
}
.features__item--picture h3 {
  color: inherit;
}

@media (max-width: 767px) {
  .features__item--picture .features__text {
    padding-top: 35px;
    padding-bottom: 35px;
  }

  .features__item--triangle {
    position: relative;
  }
  .features__item--triangle h3 {
    padding-top: 21px;
  }
  .features__item--triangle::before, .features__item--triangle::after {
    content: "";
    position: absolute;
    top: 0;
    display: block;
    width: 50%;
    height: 17px;
    box-sizing: inherit;
  }
  .features__item--triangle::before {
    left: 0;
    background: linear-gradient(to bottom left, #fff 49.6%, transparent 50.4%);
  }
  .features__item--triangle::after {
    right: 0;
    background: linear-gradient(to bottom right, #fff 49.6%, transparent 50.4%);
  }

  .features__item--grey {
    margin-top: 25px;
  }
  .features__item--grey p {
    margin-bottom: 25px;
  }
  .features__item--grey::after {
    content: "";
    display: block;
    height: 1px;
    background: #D6D6D6;
  }
}
.features__sublist {
  padding: 25px 20px 52px;
  display: grid;
  row-gap: 30px;
  background-color: #FFFFFF;
  color: #000000;
}

.features__sub-item {
  position: relative;
  margin-left: 47px;
}
.features__sub-item h4 {
  padding: 6px 0 16px;
  font-size: 18px;
  line-height: 26px;
}
.features__sub-item::before {
  display: block;
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: cover;
}
.features__sub-item--house::before {
  top: 3px;
  left: -47px;
  width: 39px;
  height: 37px;
  background-image: url("../../img/home-appart.svg");
}
.features__sub-item--food::before {
  top: 1px;
  left: -48px;
  width: 40px;
  height: 38px;
  background-image: url("../../img/eat-burger.svg");
}
.features__sub-item--gift::before {
  top: 1px;
  left: -43px;
  width: 32px;
  height: 38px;
  background-image: url("../../img/gift-souvenirs.svg");
}

.features__image {
  width: 100%;
  height: 260px;
}

@media (min-width: 768px) {
  .features__list {
    display: flex;
    flex-wrap: wrap;
  }

  .features__item--picture {
    display: grid;
    width: 100%;
  }
  .features__item--picture--right {
    grid-template-columns: 1fr 2fr;
  }
  .features__item--picture--left {
    grid-template-columns: 2fr 1fr;
  }
  .features__item--picture--left .features__text {
    order: 2;
  }

  .features__text {
    align-self: center;
    margin: 0 auto;
    padding: 40px 32px;
    max-width: 256px;
    box-sizing: border-box;
  }
  .features__text h3 {
    padding-bottom: 17px;
  }
  .features__text p {
    padding-top: 25px;
  }
  .features__text br {
    display: block;
  }

  .features__sublist {
    text-align: center;
    grid-column: 1/3;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 70px;
    padding: 60px 30px;
  }

  .features__sub-item {
    margin-left: 0;
  }
  .features__sub-item h4 {
    padding: 10px 0 25px;
  }
  .features__sub-item--house::before {
    position: static;
    top: 0;
    left: 0;
    margin: 0 auto 16px;
    width: 75px;
    height: 72px;
  }
  .features__sub-item--food::before {
    position: static;
    left: 0;
    top: 0;
    margin: 0 auto 16px;
    width: 76px;
    height: 71px;
  }
  .features__sub-item--gift::before {
    position: static;
    left: 0;
    top: 0;
    margin: 0 auto 10px;
    width: 64px;
    height: 77px;
  }
}
@media (min-width: 1200px) {
  .features__text {
    padding: 40px 55px 55px;
    max-width: 400px;
  }

  .features__sublist {
    column-gap: 100px;
    padding: 60px;
  }
}
