.form-search {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  font-weight: 700;
  line-height: 26px;
  width: 89%;
  box-sizing: border-box;
  max-width: 450px;
  padding: 20px;
  margin: 0 auto;
  background-color: #FFFFFF;
  box-shadow: 0 7px 15px 0 rgba(0, 1, 1, 0.15);
}
.form-search--hide {
  animation: form-hide 0.5s forwards;
}
.form-search--show {
  animation: form-show 0.5s forwards;
}
.form-search--error {
  animation: shake 0.5s;
}

@keyframes form-show {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes form-hide {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
    box-shadow: none;
    transform-origin: top;
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.form-search__row {
  margin-bottom: 10px;
}
.form-search__row--age {
  display: flex;
  align-items: center;
}

.form-search__age {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-search__age-wrapper {
  display: flex;
  align-items: center;
}

.form-search__date-wrapper {
  position: relative;
  width: 83%;
  max-width: 400px;
  margin: 0 auto;
}

.form-search__text {
  box-sizing: border-box;
  display: block;
  background-color: #f2f2f2;
  border: solid 2px #f2f2f2;
  width: 100%;
  margin: 0 auto;
  height: 38px;
  font-size: 14px;
  font-weight: bold;
  line-height: 26px;
  padding-left: 12px;
  text-transform: uppercase;
}
.form-search__text:hover {
  background-color: #ececec;
}
.form-search__text:focus {
  background-color: #ffffff;
  border: solid 2px #e5e5e5;
  outline: none;
}

.form-search__calendar {
  position: absolute;
  top: 5px;
  right: 10px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.form-search__calendar:hover path {
  fill: #000000;
}
.form-search__calendar:active path {
  fill: #81B3D2;
}

.form-search__number {
  margin: 0 5px;
  font-weight: 700;
  text-align: center;
  width: 38px;
  height: 38px;
  border: none;
  background-color: #f2f2f2;
}
.form-search__number:hover {
  background-color: #ececec;
}
.form-search__number:focus {
  background-color: #ffffff;
  border: solid 2px #e5e5e5;
  outline: none;
}

.form-search__submit {
  width: 89%;
  max-width: 500px;
  padding: 12px 0;
  margin: 0 auto;
  background-color: #81B3D2;
  font-weight: 700;
  color: #FFFFFF;
  font-size: 21px;
  text-transform: uppercase;
}
.form-search__submit:hover {
  background-color: #669fc1;
}
.form-search__submit:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5397be;
}

@media (min-width: 768px) {
  .form-search {
    padding: 30px 55px;
    max-width: 600px;
  }

  .form-search__row {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
  }

  .form-search__age {
    flex-direction: row;
  }

  .form-search__date-wrapper {
    margin: 0;
    max-width: 346px;
  }

  .form-search__text {
    align-self: end;
  }

  .form-search__label {
    margin-right: auto;
  }
  .form-search__label--children {
    margin-left: 53px;
  }

  .form-search__submit {
    width: 490px;
  }
}
