.filter-hotel {
  background-image: url("source/img/background-catalog.jpg");
  line-height: 21px;
  color: #FFFFFF;
}

.filter-hotel__form {
  padding: 26px 0;
  display: flex;
  justify-content: space-around;
}

.filter-hotel__set {
  margin: 0;
  padding: 0;
  border: none;
}

.filter-hotel__set legend {
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 24px;
}

.filter-hotel__item {
  padding-left: 40px;
}

.filter-hotel__item:not(.filter-hotel__item:last-child) {
  margin-bottom: 25px;
}

.filter-hotel__item label {
  position: relative;
  display: block;
  cursor: pointer;
}

.filter-hotel__input-checkbox + label::before {
  content: "";
  position: absolute;
  left: -40px;
  top: -2px;
  display: block;
  width: 23px;
  height: 23px;
  background-color: transparent;
  background-image: url("source/img/checkbox-off.svg");
  background-repeat: no-repeat;
}

.filter-hotel__input-checkbox:checked + label::before {
  width: 27px;
  background-image: url("source/img/checkbox-on.svg");
}

.filter-hotel__input-checkbox:disabled + label::before {
  background-image: url("source/img/checkbox-dis-off.svg");
}

.filter-hotel__input-checkbox:disabled + .form-filter__label::before {
  background-image: url("source/img/checkbox-dis-on.svg");
}

.filter-hotel__set--range {
  margin-left: 394px;
  display: flex;
  flex-direction: column;
}

.main-picture,
.inner-picture {
  background: url("../img/back-mobile-1x.jpg") center top no-repeat;
  position: relative;
}
.main-picture::before, .main-picture::after,
.inner-picture::before,
.inner-picture::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 50%;
  height: 33px;
  box-sizing: inherit;
}
.main-picture::before,
.inner-picture::before {
  right: 0;
  background: linear-gradient(to top left, #fff 49.6%, transparent 50.4%);
}
.main-picture::after,
.inner-picture::after {
  left: 0;
  background: linear-gradient(to top right, #fff 49.6%, transparent 50.4%);
}

.main-picture img {
  padding: 88px 0;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .main-picture img {
    width: 460px;
    height: 350px;
    padding: 77px 0;
  }
}
.review-form__wrapper {
  border: none;
  padding: 0 20px;
  position: relative;
}
.review-form__wrapper:not(:last-of-type)::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  border: 1px solid #E5E5E5;
  display: block;
  box-sizing: inherit;
}

.review-form__heading {
  color: #000000;
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  padding-top: 24px;
  padding-bottom: 20px;
}

.review-form__list li {
  margin-bottom: 16px;
}

.review-form__list--text li {
  display: flex;
  flex-direction: column;
}
.review-form__list--text li:last-child {
  margin-bottom: 35px;
}

.review-form__list--check li:last-child {
  margin-bottom: 25px;
}

.review-form__label {
  display: inline-block;
}
.review-form__label--text {
  padding-left: 16px;
}
.review-form__label--check {
  cursor: pointer;
  text-transform: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  position: relative;
  padding: 10px 0 10px 62px;
}

.review-form__input {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 16px 0 16px 16px;
  background-color: #F2F2F2;
  border: none;
}
.review-form__input--text::placeholder {
  color: #000000;
}

.review-form__radio-input + .review-form__label--check::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #F2F2F2;
}

.review-form__radio-input + .review-form__label--check::after {
  content: "";
  position: absolute;
  left: 15px;
  top: 15px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  opacity: 0;
  transition: 0.2s;
}

.review-form__radio-input:checked + .review-form__label--check:after {
  opacity: 1;
  display: block;
}

.review-form__checkbox-input + .review-form__label--check::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 46px;
  height: 46px;
  background-color: #F2F2F2;
  border-radius: 4px;
}

.review-form__checkbox-input + .review-form__label--check::after {
  content: "";
  position: absolute;
  left: 23px;
  top: 10px;
  width: 10px;
  height: 20px;
  border: 3px solid #231f20;
  border-top: none;
  border-left: none;
  transform: translateX(-50%) rotate(45deg);
  opacity: 0;
  transition: 0.2s;
}

.review-form__checkbox-input:checked + .review-form__label--check:after {
  opacity: 1;
  display: block;
}

.review-form__textarea {
  min-width: 100%;
  min-height: 150px;
  resize: none;
  border: none;
  background-color: #F2F2F2;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: rgba(0, 0, 0, 0.3);
  padding: 16px;
  margin-bottom: 56px;
}

.review-form__submit {
  font-size: 21px;
  min-width: 100%;
  padding: 15px 0;
}

.review-form__small-text {
  color: rgba(51, 51, 51, 0.6);
  font-size: 12px;
  display: block;
  text-align: end;
}

@media (min-width: 768px) {
  .review-form {
    display: flex;
    flex-wrap: wrap;
  }

  .review-form__heading {
    font-size: 21px;
  }

  .review-form__input {
    max-width: 280px;
  }

  .review-form__wrapper {
    padding: 0;
    width: 100%;
  }
  .review-form__wrapper:not(:last-of-type)::after {
    display: none;
  }

  .review-form__wrapper--columns {
    width: 50%;
  }
  .review-form__wrapper--columns:first-child::after {
    content: "";
    position: absolute;
    height: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    border: 1px solid #E5E5E5;
    display: block;
    box-sizing: inherit;
  }
}
.inner-picture {
  background-position: center bottom;
}

.inner-picture img {
  padding: 52px 0 67px;
  margin: 0 auto;
}

.inner__section-header {
  text-align: center;
  padding: 36px 20px 28px;
  position: relative;
}
.inner__section-header h2 {
  padding-bottom: 25px;
  font-size: 18px;
  line-height: 26px;
}
.inner__section-header::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  border: 1px solid #E5E5E5;
  display: block;
  box-sizing: inherit;
}
.inner__section-header br {
  display: none;
}

@media (min-width: 768px) {
  .inner__container {
    padding: 0 60px;
  }

  .inner__section-header {
    padding: 64px 0 102px;
  }
  .inner__section-header h2 {
    padding-bottom: 20px;
    font-size: 30px;
  }
  .inner__section-header p {
    line-height: 24px;
  }
  .inner__section-header br {
    display: block;
  }
  .inner__section-header::after {
    display: none;
  }
}
.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
}
.modal br {
  display: none;
}

.modal__window {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  background-color: #FFFFFF;
  padding: 45px 42px 35px;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
  min-height: 280px;
  top: 116px;
  left: 0;
  right: 0;
  text-align: center;
}
.modal__window--show {
  opacity: 1;
  visibility: visible;
}

.modal__subject {
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.modal__text {
  margin-bottom: 28px;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
}

.modal__button {
  width: 60%;
  padding: 16px 54px;
  font-size: 18px;
}

.review-form__input--error {
  background-color: rgba(169, 4, 4, 0.2);
}

@media (min-width: 410px) {
  .modal br {
    display: block;
  }
}